<template>
  <div id="app" @click="updateLastTime()">
    <router-view/>
  </div>
</template>
<script>
  export default {
    name: 'App',
    created() {
      // 启动这个定时器  
      this.isLoginOut();
    },
    methods: {
      updateLastTime(){
        this.$store.commit('setLastTime'
          , new Date().getTime());
      },
      // 判断是否超时
      isTimeOut(){
        // 页面上一次的点击时间
        this.lastTime = this.$store.state.lastTime;
        this.currentTime = new Date().getTime();
        // 超时了
        if((this.currentTime - this.lastTime) > this.sys_timeout) {
          return true;
        } else {
          return false;
        }
      },

      isLoginOut() {
        // 每次用定时器之前先清除一下  
        clearInterval(this.whole_timer);
        // 这里要备份一下this指针
        let _this = this;
        this.whole_timer = setInterval(function () {
          if (_this.isTimeOut()) {
            _this.$message.error("长时间无操作，请重新登录！");

            _this.$store.commit('delToken');
            _this.$router.push(`/login`);
            setTimeout(function() {
              clearInterval(_this.whole_timer);
            }, 1000);
          }
        }, _this.check_time);
      }


    },
    data() {
      return {
        // 上一次点击时间
        lastTime: null,
        // 当前时间
        currentTime: null,
        // 超时时间, 如果半个小时都没有点击页面就算超时
        sys_timeout: 20 * 60* 1000,
        // 检查的时间，每隔5分钟检查一次有没有超时
        check_time: 5 * 60 * 1000,
        // 计时器
        whole_timer: null

      }
    }
  }
</script>

