  methods: {
    createTimeFormatter(row) {
      if(row.createTime == null || row.createTime == "0" || row.createTime == "") {
        return ;
      }
      let date = row.createTime.split("T");
      return date[0] + " " + date[1].split(".")[0].slice(0, 5);
    },
    updateTimeFormatter(row) {
      if(row.lastUpdateTime == null || row.lastUpdateTime == "0" || row.lastUpdateTime == "") {
        return ;
      }
      let date = row.lastUpdateTime.split("T");
      return date[0] + " " + date[1].split(".")[0].slice(0, 5);
    },
    /**
     * 打开优先级弹窗
     */
    handleOpenDiglogClass() {
      this.dialogSetClassVisible = true;
      this.$api.getBusinessOrder().then(
        (res) => {
          const body = res;
          if (body.code == "200") {
            body.data.forEach((item) => {
              for (let i = 0; i < this.setClassForm.length; i++) {
                if (this.setClassForm[i].businessType == item.businessType) {
                  this.setClassForm[i].order = item.order;
                }
              }
            })

          } else {
            this.$message.error(body.message);
          }
        }
      );
    },
    /**
     * 设置业务优先级
     */
    handleSetClass() {
      const params = this.setClassForm;
      this.$api.businessOrder(params).then(
        (res) => {
          const body = res.data;
          if (body.code == "200") {
            if (body.message == null || body.message == "") {
              this.$message.success("成功！");
              this.dialogSetClassVisible = false;

            } else {
              this.$message.success(body.message);
            }

          } else {
            this.$message.error(body.message);
          }
        }
      );
    },
    /**
     * 打开体力全局配置弹窗
     */
    handleOpenDiglogConfigre() {
      this.dialogSetConfigreVisible = true;
      this.$api.getActivePowerConfigre().then(
        (res) => {
          const body = res;
          if (body.code == "200") {
            this.activePowerConfigre = {
              global: body.data.global + "",
              fruit: body.data.fruit + "",
              rock: body.data.rock + "",
            };


          } else {
            this.$message.error(body.message);
          }
        }
      );
    },
    /**
     * 
     * 全局配置
     */
    handleSetConfigre() {
      const params = this.activePowerConfigre;
      this.$api.activePowerConfigre(params).then(
        (res) => {
          const body = res.data;
          if (body.code == "200") {
            if (body.message == null || body.message == "") {
              this.$message.success("成功！");
              this.dialogSetConfigreVisible = false;

            } else {
              this.$message.success(body.message);
            }

          } else {
            this.$message.error(body.message);
          }
        }
      );
    },
    handleDelete(boostingName) {
      const params = {
        orderId: this.id,
        businessType: boostingName,
      };
      this.$api.boostingDelete(params).then(
        (res) => {
          const body = res.data;
          if (body.code == "200") {
            this.refesh();
            if (body.message == null || body.message == "") {
              this.$message.success("成功！");
            } else {
              this.$message.success(body.message);
            }

          } else {
            this.$message.error(body.message);
          }
        }
      );
    },
    handleDeleteMaterials(boostingName, name) {
      const params = {
        orderId: this.id,
        businessType: boostingName,
        boostingTask: "0",
        progress: name,
        target: "1",
        operateType: "DELETE"
      };
      this.$api.boostingDeleteArr(params).then(
        (res) => {
          const body = res.data;
          if (body.code == "200") {
            this.refesh();
            if (body.message == null || body.message == "") {
              this.$message.success("成功！");
            } else {
              this.$message.success(body.message);
            }

          } else {
            this.$message.error(body.message);
          }
        }
      );
    },
    
    handleDeleteArr(boostingName, delkey) {
      const params = {
        orderId: this.id,
        businessType: boostingName,
        boostingTask: "0",
        progress: delkey,
        target: "1",
        operateType: "DELETE"
      };
      this.$api.boostingDeleteArr(params).then(
        (res) => {
          const body = res.data;
          if (body.code == "200") {
            this.refesh();
            if (body.message == null || body.message == "") {
              this.$message.success("成功！");
            } else {
              this.$message.success(body.message);
            }

          } else {
            this.$message.error(body.message);
          }
        }
      );
    },
    /**
     * 请求
     */
    requestBaseInfo() {
      const params = {
        orderId: this.id,
      };
      this.$api.getBaseInfo(params).then(
        (res) => {
          const body = res.data;
          this.baseInfoData = [];
          this.baseInfoData.push({
            playerAccount: body.nickName,
            class: body.gameLevel,
            QP: "/",
            stone: body.rockAtCurrent,
            fruitAtCurrent: body.fruitAtCurrent,

            id: body.uidOfFriend,
          });
          if (res.code != "200") {
            this.$message.error(body.message);
          }
        }
      );
    },
    requestBusinessProgress() {
      const params = {
        orderId: this.id,
      };
      this.$api.getBusinessProgress(params).then(
        (res) => {
          const body = res.data;

          this.signInInfoData = [res.data.signIn]
          if (this.signInInfoData != null) {
            this.signInInfoData.forEach(item => {
              if (item != null) {
                item.status = this.statusMap[item.status];
              }
            });
          }

          this.boostingLevelsData = res.data.boostingLevels
          this.boostingLevelsData.forEach(item => {
            item.type = this.boostingLevelMap[item.type];
            item.status = this.statusMap[item.status];
          });

          this.gatherBallsData = [res.data.gatherBalls]
          if (this.gatherBallsData != null) {
            this.gatherBallsData.forEach(item => {
              if (item != null) {
                item.status = this.statusMap[item.status];
              }
            });
          }

          this.gatherDogFoodData = [res.data.gatherDogFood]
          if (this.gatherDogFoodData != null) {
            this.gatherDogFoodData.forEach(item => {
              if (item != null) {
                item.status = this.statusMap[item.status];
              }
            });
          }

          this.gatherQPData = [res.data.gatherQP]
          if (this.gatherQPData != null) {
            this.gatherQPData.forEach(item => {
              if (item != null) {
                item.status = this.statusMap[item.status];
              }
            });
          }

          this.gatherMaterialsData = res.data.gatherMaterials
          if (this.gatherMaterialsData != null) {
            this.gatherMaterialsData.forEach(item => {
              if (item != null) {
                item.status = this.statusMap[item.status];
              }
            });
          }

          this.gatherGreenSquareData = [res.data.gatherGreenSquare]
          if (this.gatherGreenSquareData != null) {
            this.gatherGreenSquareData.forEach(item => {
              if (item != null) {
                item.status = this.statusMap[item.status];
              }
            });
          }

          this.boostingEventsData = res.data.boostingEvents
          if (this.boostingEventsData != null) {
            this.boostingEventsData.forEach(item => {
              if (item != null) {
                item.status = this.statusMap[item.status];
              }
            });
          }

          this.dailyInfoData = [res.data.daily]
          if (this.dailyInfoData != null) {
            this.dailyInfoData.forEach(item => {
              if (item != null) {
                item.status = this.statusMap[item.status];
              }
            });
          }
          // this.boostingHuntingInfoData = res.data.boostingHuntingInfo
          this.skillInfoData = res.data.follower
          if (this.skillInfoData != null) {
            this.skillInfoData.forEach(item => {
              if (item != null) {
                item.status = this.statusMap[item.status];
              }
            });
          }

          this.followerFettersInfoData = res.data.followerFetters
          if (this.followerFettersInfoData != null) {
            this.followerFettersInfoData.forEach(item => {
              if (item != null) {
                item.status = this.statusMap[item.status];
              }
            });
          }

          this.purchaseLevelsInfoData = res.data.purchaseLevels
          if (this.purchaseLevelsInfoData != null) {
            this.purchaseLevelsInfoData.forEach(item => {
              if (item != null) {
                item.status = this.statusMap[item.status];
              }
            });
          }

          this.signInInfoFlag = res.data.signIn == null ? false : true
          this.boostingLevelsFlag = res.data.boostingLevels.length == 0 ? false : true
          this.gatherBallsFlag = res.data.gatherBalls == null ? false : true
          this.gatherDogFoodFlag = res.data.gatherDogFood == null ? false : true
          this.gatherQPFlag = res.data.gatherQP == null ? false : true
          this.gatherMaterialsFlag = res.data.gatherMaterials == null ? false : true
          this.gatherGreenSquareFlag = res.data.gatherGreenSquare == null ? false : true
          this.boostingEventsFlag = res.data.boostingEvents == null ? false : true
          this.dailyInfoFlag = res.data.daily == null ? false : true
          // this.boostingHuntingInfoFlag = res.data.boostingHuntingInfo == [] ? false : true
          this.skillInfoFlag = res.data.follower == null ? false : true
          this.followerFettersInfoFlag = res.data.followerFetters == null ? false : true
          this.purchaseLevelsInfoFlag = res.data.purchaseLevels == null ? false : true

          if (res.code == "200") {
            if (res.message == null || res.message == "") {
              this.$message.success("成功！");
            } else {
              this.$message.success(res.message);
            }

          } else {
            this.$message.error(body.message);
          }
        }
      );
    },
    /**
     * 刷新页面
     */
    refesh() {
      this.requestBaseInfo();
      this.requestBusinessProgress();
    }
  }
};
