<template>
  <div class="Balls">
    <div class="header">
      <el-breadcrumb class="bread" separator-class="el-icon-arrow-right">
        <el-breadcrumb-item>业务管理</el-breadcrumb-item>
        <el-breadcrumb-item>搓丸子</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class="content">
      <el-form
        :model="ruleForm"
        status-icon
        :rules="rules"
        ref="ruleForm"
        label-width="80px"
        class="ruleForm"
      >
        <el-form-item label="丸子数" prop="balls" :label-width="formLabelWidth" required>
          <el-input v-model="ruleForm.balls" autocomplete="off" style="width: 200px"></el-input>
        </el-form-item>
        <el-form-item :label-width="formLabelWidth">
          <el-button type="primary" size="mini" @click="submitForm('ruleForm')"
            >保存</el-button
          >
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>
  
<script>
export default {
  name: "Balls",
  components: {},
  created() {

  },
  watch: {

  },
  data() {
    var validateBalls = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请输入丸子数"));
      } else {
        callback();
      }
    };
    

    return {
      id: this.$route.query.id,
      ruleForm: {
        balls: "",
      },
      rules: {
        balls: [{ validator: validateBalls, trigger: "blur" }],
      },
      formLabelWidth: "80px",

    };
  },

  methods: {
    /**
     * 提交表单
     */
     submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.request();
        } else {
          this.$message.error("请检查输入！");
          return false;
        }
      });
    },
    request() {
      const pro =  { //搓丸子
        "ballsCount": this.ruleForm.balls, //总数量 
        "completedBalls": 0, //已完成的
        "status": "N", //签到任务的状态  未执行N 已执行完毕D 执行出现异常E
        "createTime": Date.now() + 8 * 60 * 60 * 1000, // long型 当前时间毫秒数
        "lastUpdateTime": ""
    }
      const params = {
        orderId: this.id,
        boostingTask: this.ruleForm.balls+ '|' + "搓丸子",
        progress: JSON.stringify(pro),
        target: this.ruleForm.balls,
        operateType: "ADD",
        businessType: "GatherBalls"
      };
      this.$api.taskBoosting(params).then(
        (res) => {
          const body = res.data;
          if (body.code == "200") {
            if(body.message == null || body.message == "") {
              this.$message.success("成功！");
            } else {
              this.$message.success(body.message);
            }
          } else {
            this.$message.error(body.message);
          }
        }
        
      );
    },
    
  }
};
</script>
  
<style scoped lang="less">
.header {
  padding: 20px 100px 0 20px;
  height: 50px;
}
.content {
  padding: 0px 100px 0 20px;
  
}
</style>
  