<template>
  <div class="Activity">
    <div class="header">
      <el-breadcrumb class="bread" separator-class="el-icon-arrow-right">
        <el-breadcrumb-item>业务管理</el-breadcrumb-item>
        <el-breadcrumb-item>挂活动</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class="content">
      <el-form :model="ruleForm" status-icon :rules="rules" ref="ruleForm" label-width="80px" class="ruleForm">
        <el-form-item label="活动">
          <el-radio-group v-model="ruleForm.activity">
            <el-radio label="恰赫季斯·万圣节·三部曲">恰赫季斯·万圣节·三部曲</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="代练内容">
          <el-radio-group v-model="ruleForm.content">
            <el-radio label="机械伊丽亲①号机">机械伊丽亲①号机</el-radio>
            <el-radio label="机械伊丽亲②号机">机械伊丽亲②号机</el-radio>
            <el-radio label="活动毕业">活动毕业</el-radio>
            <el-radio label="活动毕业（不搬棋子）">活动毕业（不搬棋子）</el-radio>
            <el-radio label="活动通关">活动通关</el-radio>
            <el-radio label="活动池子">活动池子</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="池子数" prop="pool" required>
          <el-input v-model="ruleForm.pool" autocomplete="off" style="width: 200px"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" size="mini" @click="submitForm('ruleForm')">保存</el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>
  
<script>
export default {
  name: "Activity",
  components: {},
  created() {

  },
  watch: {

  },
  data() {
    var validatePool = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请输入"));
      } else {
        callback();
      }
    };


    return {
      id: this.$route.query.id,

      ruleForm: {
        activity: "",
        content: "",
        pool: "0",
      },
      rules: {
        pool: [{ validator: validatePool, trigger: "blur" }],
      },
      formLabelWidth: "80px",
    };
  },

  methods: {
    /**
     * 提交表单
     */
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.request();
        } else {
          this.$message.error("请检查输入！");
          return false;
        }
      });
    },
    request() {
      const pro = [{
        "eventName": this.ruleForm.activity, //活动名
        "boostingContent": this.ruleForm.content, //代练内容
        "progress": "0/0", //进度
        "status": "N", //签到任务的状态  未执行N 已执行完毕D 执行出现异常E
        "createTime": Date.now() + 8 * 60 * 60 * 1000, // long型 当前时间毫秒数
        "lastUpdateTime": ""
      }];
      const params = {
        orderId: this.id,
        boostingTask: "活动=" + this.ruleForm.activity +
          '|代练内容=' + this.ruleForm.content +
          '|池子数=' + this.ruleForm.pool + '|' + "毕业活动",
        operateType: "ADD",
        progress: JSON.stringify(pro),
        target: this.ruleForm.content,
        businessType: "BoostingEvents"
      };
      this.$api.taskBoosting(params).then(
        (res) => {
          const body = res.data;
          if (body.code == "200") {
            if(body.message == null || body.message == "") {
              this.$message.success("成功！");
            } else {
              this.$message.success(body.message);
            }
          } else {
            this.$message.error(body.message);
          }
        }

      );
    },

  }
};
</script>
  
<style scoped lang="less">
.header {
  padding: 20px 100px 0 20px;
  height: 50px;
}

.content {
  padding: 0px 100px 0 20px;

  .ruleForm {}
}
</style>
  