<template>
  <div class="Skills">
    <div class="header">
      <el-breadcrumb class="bread" separator-class="el-icon-arrow-right">
        <el-breadcrumb-item>业务管理</el-breadcrumb-item>
        <el-breadcrumb-item>从者及从者技能满破</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class="content">
      <div class="content-inner">
        <!-- 从者及从者技能满破列表 -->
        <div class="list">
          <el-table stripe border size="mini" :data="tableData" style="width: 100%" :max-height="tableMaxHeight">
            <el-table-column v-for="(item, i) in tableHeader" :prop="item.key" :label="item.name" :key="i">
            </el-table-column>
            <el-table-column label="等级满破">
              <template slot-scope="scope">
                <el-checkbox v-model="scope.row.fullBreakClass">等级满破</el-checkbox>
              </template>
            </el-table-column>
            <el-table-column label="技能满破">
              <template slot-scope="scope">
                <el-checkbox v-model="scope.row.fullBreakSkill">技能满破</el-checkbox>
              </template>
            </el-table-column>
            <el-table-column label="圣杯转临">
              <template slot-scope="scope">
                <el-checkbox v-model="scope.row.grailCheck">圣杯转临:</el-checkbox>
                <el-input size="mini" v-model="scope.row.grailNum" autocomplete="off">100</el-input>

              </template>
            </el-table-column>
            <el-table-column fixed="right" label="操作" width="80">
              <template slot-scope="scope">
                <el-button size="mini" type="primary" @click="handleAdd(scope.row)">添加
                </el-button>

              </template>
            </el-table-column>
          </el-table>
        </div>
      </div>
      <div class="content-footer">
      </div>
    </div>
  </div>
</template>
  
<script>
export default {
  name: "Skills",
  components: {},
  created() {
    if (window.innerHeight < 900) {
      this.tableMaxHeight = "500px";
    } else if (window.innerHeight >= 900) {
      this.tableMaxHeight = "700px";
    }
    this.handleUpdate();
  },
  watch: {},
  data() {
    return {
      id: this.$route.query.id,

      tableMaxHeight: "",
      tableHeader: [
        {
          key: "career",
          name: "职业",
        },
        {
          key: "name",
          name: "名称",
        },
        {
          key: "star",
          name: "星级",
        },
        {
          key: "come",
          name: "再临",
        },
        {
          key: "class",
          name: "等级",
        },
        {
          key: "skill1",
          name: "技能1",
        },
        {
          key: "skill2",
          name: "技能2",
        },
        {
          key: "skill3",
          name: "技能3",
        }
      ],
      tableData: [],
    };
  },

  methods: {
    handleUpdate() {
      const params = {
        orderId: this.id,
      };
      this.$api.getInfoFollower(params).then(
        (res) => {
          const body = res.data;
          if (res.code == "200") {
            this.tableData = [];
            for (let i = 0; i < body.follower.length; i++) {
              let f = body.follower[i];
              this.tableData.push({
                career: f.profession,
                name: f.followerName,
                star: f.starLevel,
                come: f.comeAgainLevel,
                class: f.followerLevel,
                skill1: f.skill1Level,
                skill2: f.skill2Level,
                skill3: f.skill3Level,
                fullBreakClass: f.levelBreak,
                fullBreakSkill: f.skillBreak,
                grailCheck: f.holyGrailChange,
                grailNum: f.holyGrailTotal,
              })
            }
            if(res.message == null || res.message == "") {
              this.$message.success("成功！");
            } else {
              this.$message.success(res.message);
            }

          } else {
            this.$message.error(body.message);
          }
        }

      );
    },
    /**
     * 提交表单
     */
    handleAdd(row) {

      let data = "职业=" + row.career +
        "|名称=" + row.name +
        "|星级=" + row.star +
        "|再临=" + row.come +
        "|等级=" + row.class +
        "|技能1=" + row.skill1 +
        "|技能2=" + row.skill2 +
        "|技能3=" + row.skill3 +
        "|等级满破=" + row.fullBreakClass +
        "|技能满破=" + row.fullBreakSkill +
        "|圣杯转临=" + row.grailNum;
      const pro = [{
        "profession": row.career,
        "followerName": row.name,
        "starLevel": row.star,
        "comeAgainLevel": row.come, //再临
        "followerLevel": row.class, //等级
        "skill1Level": row.skill1, //技能1等级
        "skill2Level": row.skill2,
        "skill3Level": row.skill3,
        "levelBreak": row.fullBreakClass, //等级满破
        "skillBreak": row.fullBreakSkill, //技能满破
        "holyGrailChange": row.grailCheck, //圣杯转临
        "holyGrailTotal": row.grailNum, //圣杯转临
        "status": "N", //签到任务的状态  未执行N 已执行完毕D 执行出现异常E
        "createTime": Date.now() + 8 * 60 * 60 * 1000, // long型 当前时间毫秒数
        "lastUpdateTime": ""
      }]
      const params = {
        orderId: this.id,
        boostingTask: data + '|' + "从者及从者技能满破",
        operateType: "ADD",
        progress: JSON.stringify(pro),
        target: row.fullBreakClass + '|' + row.fullBreakSkill + '|' + row.grailCheck + '|' + row.grailNum,

        businessType: "Follower"
      };
      this.$api.taskBoosting(params).then(
        (res) => {
          const body = res.data;
          if (body.code == "200") {
            this.$message.success("成功！");

          } else {
            this.$message.error(body.message);
          }
        }

      );



    },
  },
};
</script>
  
<style scoped lang="less">
.header {
  padding: 20px 100px 0 20px;
  height: 50px;
}

.content {
  padding: 0px 100px 0 20px;

  .ruleForm {
    width: 300px;
  }
}
</style>
  