<template>
  <div class="PMField">
    <div class="header">
      <el-breadcrumb class="bread" separator-class="el-icon-arrow-right">
        <el-breadcrumb-item>业务管理</el-breadcrumb-item>
        <el-breadcrumb-item>推图</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class="content">
      <el-card class="box-card">
        <div slot="header" class="clearfix">
          <span>修炼场</span>
          <el-button size="mini" style="margin-left:10px;" type="primary" @click="handlePM">保存</el-button>
        </div>
        <div class="card-content">
          <table>
            <tr>
              <th colspan="2">
                <el-checkbox v-model="checkth" @change="handleCheckAllChange(-1)">
                  修炼场
                </el-checkbox>
              </th>
            </tr>
            <tr v-for="(item, index) in data" :key="index">
              <td class="tableHeader">
                <el-checkbox v-model="checkAll[index]" @change="handleCheckAllChange(index)">{{
                    item.children[0].label
                }}
                </el-checkbox>

              </td>
              <td class="tableContent">
                <el-checkbox-group v-model="checkedList[index]">
                  <el-checkbox v-for="jtem in item.children[0].children" :label="jtem.label" :key="jtem.label">
                    {{ jtem.label }}
                  </el-checkbox>
                </el-checkbox-group>
              </td>
            </tr>

          </table>

        </div>
      </el-card>
    </div>
  </div>
</template>
  
<script>
const PMList = [
  "周日 剑之修炼场 初级,周日 剑之修炼场 中级,周日 剑之修炼场 上级,周日 剑之修炼场 超级",
  "周一 弓之修炼场 初级,周一 弓之修炼场 中级,周一 弓之修炼场 上级,周一 弓之修炼场 超级",
  "周二 枪之修炼场 初级,周二 枪之修炼场 中级,周二 枪之修炼场 上级,周二 枪之修炼场 超级",
  "周三 狂之修炼场 初级,周三 狂之修炼场 中级,周三 狂之修炼场 上级,周三 狂之修炼场 超级",
  "周四 骑之修炼场 初级,周四 骑之修炼场 中级,周四 骑之修炼场 上级,周四 骑之修炼场 超级",
  "周五 术之修炼场 初级,周五 术之修炼场 中级,周五 术之修炼场 上级,周五 术之修炼场 超级",
  "周六 暗之修炼场 初级,周六 暗之修炼场 中级,周六 暗之修炼场 上级,周六 暗之修炼场 超级",
];
const listHeader = [
  "周日",
  "周一",
  "周二",
  "周三",
  "周四",
  "周五",
  "周六",
];

const captial = [
  "周日",
  "周一",
  "周二",
  "周三",
  "周四",
  "周五",
  "周六",
];
export default {
  name: "PMField",
  components: {},
  created() {

    for (let i = 0; i < PMList.length; i++) {
      let list = PMList[i].split(",");
      this.checkedOpt.push(list);
      let nodes = [];
      this.data.push({
        label: captial[i],
        children: []
      });
      for (let m = 0; m < list.length; m++) {
        nodes.push({
          label: list[m],
          children: []
        });
      }
      this.data[i].children.push({
        label: listHeader[i],
        children: nodes
      })
    }
  },
  watch: {},
  computed: {
  },
  data() {

    return {
      id: this.$route.query.id,

      checkAll: [
        false,
        false,
        false,
        false,
        false,
        false,
        false,
      ],
      checkth: false,
      checkedList: [[], [], [], [], [], [], []],
      checkedOpt: [],
      data: [],
      defaultProps: {
        children: 'children',
        label: 'label'
      },
    };
  },

  methods: {
    handleCheckAllChange(index) {
      if (index == -1) {
        this.checkedList = this.checkth ? this.checkedOpt : [[], [], [], [], [], [], [],];
      } else {
        this.checkedList[index] = this.checkAll[index] ? this.checkedOpt[index] : [];
      }
    },
    handlePM() {
      let cheakList = this.checkedList;
      let data = [];
      for (let i = 0; i < cheakList.length; i++) {
        if (cheakList[i].length == 0) continue;
        // data.push(cheakList[i].join('') + '|' + listHeader[i]);
        data.push(cheakList[i].join(',') + '|' + '修炼场');
      }

      if (data.length == 0) return;
      let promiseArr = [];
      const pro = [{
        "type": "BoostingLevelsOfPractice", //推图的类型 推图 BoostingLevels
        // 推图（修炼场）BoostingLevelsOfPractice
        // 推图（幕间物语）BoostingLevelsOfStory
        // 推图（强化任务）BoostingLevelsOfStrengthen
        // 推图（狩猎）BoostingLevelsOfHunting
        "progress": "0/688", //对应推图类型的进度
        "status": "N", //签到任务的状态  未执行N 已执行完毕D 执行出现异常E
        "createTime": Date.now() + 8 * 60 * 60 * 1000, // long型 当前时间毫秒数
        "lastUpdateTime": ""
      }]
      for (let i = 0; i < data.length; i++) {
        const params = {
          orderId: this.id,
          boostingTask: data[i],
          operateType: "ADD",
          progress: JSON.stringify(pro),
          target: 688,

          businessType: "BoostingLevelsOfPractice"
        };
        promiseArr.push(this.$api.taskBoosting(params));

      }
      Promise.all(promiseArr).then((res) => {
        for (let i = 0; i < res.length; i++) {
          const body = res[i].data;
          if (body.code != "200") {
            this.$message.error(body.message);
          }
        }
        this.$message.success("成功！");
      });
    },
  },
};
</script>

