<template>
  <div class="Slave">
    <div class="header">
      <el-breadcrumb class="bread" separator-class="el-icon-arrow-right">
        <el-breadcrumb-item>业务管理</el-breadcrumb-item>
        <el-breadcrumb-item>从者羁绊</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class="content">
      <div class="content-inner">
        <!-- 从者羁绊列表 -->
        <div class="list">
          <el-table stripe border size="mini" :data="tableData" style="width: 100%" :max-height="tableMaxHeight">
            <el-table-column v-for="(item, i) in tableHeader" :prop="item.key" :label="item.name" :key="i">
            </el-table-column>

            <el-table-column label="目标羁绊等级">
              <template slot-scope="scope">
                <el-input size="mini" v-model="scope.row.targetClass" autocomplete="off"></el-input>
              </template>
            </el-table-column>
            <el-table-column fixed="right" label="操作" width="80">
              <template slot-scope="scope">
                <el-button size="mini" type="primary" @click="handleAdd(scope.row)">添加
                </el-button>
              </template>
            </el-table-column>
          </el-table>
        </div>
      </div>
      <div class="content-footer">

      </div>
    </div>
  </div>
</template>
  
<script>
export default {
  name: "Slave",
  components: {},
  created() {
    if (window.innerHeight < 900) {
      this.tableMaxHeight = "500px";
    } else if (window.innerHeight >= 900) {
      this.tableMaxHeight = "700px";
    }
    this.handleUpdate();
  },
  watch: {},
  data() {
    return {
      id: this.$route.query.id,

      tableMaxHeight: "",
      tableHeader: [
        {
          key: "career",
          name: "职业",
        },
        {
          key: "name",
          name: "名称",
        },
        {
          key: "star",
          name: "星级",
        },
        {
          key: "curClass",
          name: "当前羁绊等级",
        },
      ],


      tableData: [],
    };
  },

  methods: {
    handleUpdate() {
      const params = {
        orderId: this.id,
      };
      this.$api.getInfoFollower(params).then(
        (res) => {
          const body = res.data;
          if (res.code == "200") {
            this.tableData = [];
            for (let i = 0; i < body.followerFetters.length; i++) {
              let f = body.followerFetters[i];
              this.tableData.push({
                career: f.profession,
                name: f.followerName,
                star: f.starLevel,
                curClass: f.fettersLevel,
                targetClass: f.finalFettersLevel,
              })
            }
            if(res.message == null || res.message == "") {
              this.$message.success("成功！");
            } else {
              this.$message.success(res.message);
            }

          } else {
            this.$message.error(body.message);
          }
        }

      );
    },
    /**
     * 提交表单
     */
    handleAdd(row) {
      let data = "职业=" + row.career +
        "|名称=" + row.name +
        "|星级=" + row.star +
        "|当前羁绊等级=" + row.curClass +
        "|目标羁绊等级=" + row.targetClass;
      const pro = [{
        "profession": row.career, //职业
        "followerName": row.name, //从者名称
        "starLevel": row.star, //星级
        "fettersLevel": row.curClass, //当前羁绊等级
        "finalFettersLevel": row.targetClass,
        "status": "N", //签到任务的状态  未执行N 已执行完毕D 执行出现异常E
        "createTime": Date.now() + 8 * 60 * 60 * 1000, // long型 当前时间毫秒数
        "lastUpdateTime": ""
      }]
      const params = {
        orderId: this.id,
        boostingTask: data + '|' + "从者羁绊",
        progress: JSON.stringify(pro),
        target: row.targetClass,

        operateType: "ADD",
        businessType: "FollowerFetters"
      };
      this.$api.taskBoosting(params).then(
        (res) => {
          const body = res.data;
          if (body.code == "200") {
            this.$message.success("成功！");

          } else {
            this.$message.error(body.message);
          }
        }

      );



    },
  },
};
</script>
  
<style scoped lang="less">
.header {
  padding: 20px 100px 0 20px;
  height: 50px;
}

.content {
  padding: 0px 100px 0 20px;

  .ruleForm {
    width: 300px;
  }
}
</style>
  