<template>
  <div class="Daily">
    <div class="header">
      <el-breadcrumb class="bread" separator-class="el-icon-arrow-right">
        <el-breadcrumb-item>业务管理</el-breadcrumb-item>
        <el-breadcrumb-item>挂日常</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class="content">
      <el-form :model="ruleForm" status-icon :rules="rules" ref="ruleForm" label-width="80px" class="ruleForm">
        <el-form-item label="天数" prop="days" required>
          <el-input v-model="ruleForm.days" autocomplete="off" style="width: 200px"></el-input>
        </el-form-item>
        <el-form-item label="包活动">
          <el-radio-group v-model="ruleForm.contain">
            <el-radio label="不包">不包</el-radio>
            <el-radio label="包（不限棋子）">包（不限棋子）</el-radio>
            <el-radio label="包（限棋子）">包（限棋子）</el-radio>
          </el-radio-group>
        </el-form-item>

        <el-form-item>
          <el-button size="mini" type="primary" @click="submitForm('ruleForm')">保存</el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>
  
<script>
export default {
  name: "Daily",
  components: {},
  created() { },
  watch: {},
  data() {
    var validateDays = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请输入天数"));
      } else {
        callback();
      }
    };

    return {
      id: this.$route.query.id,

      ruleForm: {
        days: "",
        contain: "",
      },
      rules: {
        days: [{ validator: validateDays, trigger: "blur" }],
      },
    };
  },

  methods: {
    /**
     * 提交表单
     */
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.request();
        } else {
          this.$message.error("请检查输入！");
          return false;
        }
      });
    },
    request() {
      const pro = { //日常
        "days": this.ruleForm.days, //总天数
        "alsoEvents": this.ruleForm.contain == "不包" ? false :true, //是否包活动
        "doneDays": 0, //已完成天数
        "giftPoolCount": 0, //送池数
        "donePoolCount": 0, //已打池子数
        "status": "N", //签到任务的状态  未执行N 已执行完毕D 执行出现异常E
        "createTime": Date.now() + 8 * 60 * 60 * 1000, // long型 当前时间毫秒数
        "lastUpdateTime": ""
      }
      const params = {
        orderId: this.id,
        boostingTask: "天数=" + this.ruleForm.days +
          '|包活动=' + this.ruleForm.contain + '|' + "日常",
        progress: JSON.stringify(pro),
        target: this.ruleForm.days,

        operateType: "ADD",
        businessType: "Daily"
      };
      this.$api.taskBoosting(params).then(
        (res) => {
          const body = res.data;
          if (body.code == "200") {
            if(body.message == null || body.message == "") {
              this.$message.success("成功！");
            } else {
              this.$message.success(body.message);
            }
          } else {
            this.$message.error(body.message);
          }
        }

      );
    },
  },
};
</script>
  
<style scoped lang="less">
.header {
  padding: 20px 100px 0 20px;
  height: 50px;
}

.content {
  padding: 0px 100px 0 20px;

}
</style>
  