<template>
  <div class="DogFood">
    <div class="header">
      <el-breadcrumb class="bread" separator-class="el-icon-arrow-right">
        <el-breadcrumb-item>业务管理</el-breadcrumb-item>
        <el-breadcrumb-item>捞金狗粮</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class="content">
      <el-form :model="ruleForm" status-icon :rules="rules" ref="ruleForm" label-width="100px" class="ruleForm">
        <el-form-item label="数量（车）" prop="number" required>
          <el-input v-model="ruleForm.number" autocomplete="off" style="width: 200px"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" size="mini" @click="submitForm('ruleForm')">保存</el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>
  
<script>
export default {
  name: "DogFood",
  components: {},
  created() { },
  watch: {},
  data() {
    var validateNumber = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请输入数量"));
      } else {
        callback();
      }
    };

    return {
      id: this.$route.query.id,

      ruleForm: {
        number: "",
      },
      rules: {
        number: [{ validator: validateNumber, trigger: "blur" }],
      },
    };
  },

  methods: {
    /**
     * 提交表单
     */
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.request();
        } else {
          this.$message.error("请检查输入！");
          return false;
        }
      });
    },
    request() {
      const pro = { //狗粮
        "total": this.ruleForm.number, //总数
        "doneCount": 0, //已完成的
        "fightCount": 0, //已打次数
        "activePowerCost": 0, //累计消耗体力
        "status": "N", //签到任务的状态  未执行N 已执行完毕D 执行出现异常E
        "createTime": Date.now() + 8 * 60 * 60 * 1000, // long型 当前时间毫秒数
        "lastUpdateTime": ""
      }
      const params = {
        orderId: this.id,
        boostingTask: this.ruleForm.number + '|' + "狗粮",
        operateType: "ADD",
        progress: JSON.stringify(pro),
        target: this.ruleForm.number,

        businessType: "GatherDogFood"
      };
      this.$api.taskBoosting(params).then(
        (res) => {
          const body = res.data;
          if (body.code == "200") {
            if(body.message == null || body.message == "") {
              this.$message.success("成功！");
            } else {
              this.$message.success(body.message);
            }
          } else {
            this.$message.error(body.message);
          }
        }

      );
    },
  },
};
</script>
  
<style scoped lang="less">
.header {
  padding: 20px 100px 0 20px;
  height: 50px;
}

.content {
  padding: 0px 100px 0 20px;

  .ruleForm {
    width: 400px;
  }
}
</style>
  