


<template>
  <div class="Material">
    <div class="header">
      <el-breadcrumb class="bread" separator-class="el-icon-arrow-right">
        <el-breadcrumb-item>业务管理</el-breadcrumb-item>
        <el-breadcrumb-item>打材料</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class="content">
      <el-card class="box-card">
        <div slot="header" class="clearfix">
          <span>打材料</span>
          <el-button size="mini" style="margin-left:10px;" type="primary" @click="handlePM">保存</el-button>
        </div>
        <div class="card-content">
          <table>
            <tr v-for="(item, index) in data" :key="index">
              <td class="tableHeader">
                <el-checkbox v-model="checkAll[index]" @change="handleCheckAllChange(index)">{{
                    item.children[0].label
                }}
                </el-checkbox>

              </td>
              <td class="tableContent">
                <el-checkbox-group v-model="checkedList[index]">
                  <el-checkbox v-for="(jtem, i) in item.children[0].children" :label="i" :key="jtem.label">
                    {{ jtem.label }}
                    <input class="tableInput" type="text" v-model="jtem.value" placeholder="请输入内容" />
                  </el-checkbox>
                </el-checkbox-group>
              </td>
            </tr>

          </table>

        </div>
      </el-card>
    </div>
  </div>
</template>
  
<script>
const PMList = [
  "凤凰羽毛,世界树之种,英雄之证,虚影之尘,龙之逆鳞,混沌之爪,鬼魂提灯,蛇之宝玉,无间齿轮,禁断书页,龙之牙,陨蹄铁,人工生命体幼体,八连双晶,祸骨/凶骨,蛮神心脏,精灵根,战马的幼角,血之泪石,黑兽脂,愚者之锁,封魔之灯,大骑士勋章,智慧之圣甲虫像,追忆的贝壳,万死的毒针,起源的胎毛,咒兽胆石,魔术髓液,奇奇神酒,枯淡勾玉,宵泣之铁桩,振荡火药,永远结冰,极光之钢,巨人的戒指,闲古铃,晓光炉心,九十九镜,祸罪之箭头,真理之卵,光银之冠,煌星碎片,神脉灵子,悠久果实,虹之线球,鬼炎鬼灯,赦免的小钟,梦幻的鳞粉",
  "剑之辉石,弓之辉石,枪之辉石,骑之辉石,术之辉石,暗之辉石/杀,狂之辉石,剑之魔石,弓之魔石,枪之魔石,骑之魔石,术之魔石,暗之魔石,狂之魔石,剑之秘石,弓之秘石,枪之秘石,骑之秘石,术之秘石,暗之秘石,狂之秘石,剑阶银棋,弓阶银棋,枪阶银棋,骑阶银棋,术阶银棋,暗阶银棋,狂阶银棋,剑阶金像,弓阶金像,枪阶金像,骑阶金像,术阶金像,暗阶金像,狂阶金像",
];
const listHeader = [
  "类型11",
  "类型12",
];

const captial = [
  "类型11",
  "类型12",
];
export default {
  name: "Material",
  components: {},
  created() {

    for (let i = 0; i < PMList.length; i++) {
      let list = PMList[i].split(",");
      this.checkedOpt.push(list);
      let nodes = [];
      this.data.push({
        label: captial[i],
        children: []
      });
      for (let m = 0; m < list.length; m++) {
        nodes.push({
          label: list[m],
          value: 0,
          children: []
        });
      }
      this.data[i].children.push({
        label: listHeader[i],
        children: nodes
      })
    }
  },
  watch: {},
  computed: {
  },
  data() {

    return {
      id: this.$route.query.id,
      checkAll: [
        false,
        false,
      ],
      checkedList: [[], []],
      checkedOpt: [],
      data: [],
      defaultProps: {
        children: 'children',
        label: 'label'
      },
    };
  },

  methods: {
    handleCheckAllChange(index) {
      this.checkedList[index] = [];
      if (this.checkAll[index]) {
        for (let i = 0; i < this.checkedOpt[index].length; i++) {
          this.checkedList[index].push(i);
        }
      }
    },
    handlePM() {
      let cheakList = this.checkedList;
      let data = "";
      let temp = "";

      for (let i = 0; i < cheakList.length; i++) {
        if (cheakList[i].length == 0) continue;
        for (let j = 0; j < cheakList[i].length; j++) {
          if (i == 0 && j == 0) {
            temp += this.data[i].children[0].children[cheakList[i][j]].label + "=" + this.data[i].children[0].children[cheakList[i][j]].value;

          } else {
            temp += "|" + this.data[i].children[0].children[cheakList[i][j]].label + "=" + this.data[i].children[0].children[cheakList[i][j]].value;

          }
        }
      }
      data = temp + '|' + '素材';

      const pro = []
      // let targetNum = "";
      for (let i = 0; i < cheakList.length; i++) {
        for (let j = 0; j < cheakList[i].length; j++) {
          pro.push({
            "materialName": this.data[i].children[0].children[cheakList[i][j]].label, //材料名
            "total": this.data[i].children[0].children[cheakList[i][j]].value, //总数
            "doneCount": 0, //已完成的
            "activePowerCost": 0, //累计消耗体力
            "status": "N", //签到任务的状态  未执行N 已执行完毕D 执行出现异常E
            "createTime": Date.now() + 8 * 60 * 60 * 1000, // long型 当前时间毫秒数
            "lastUpdateTime": ""
          })
          // if(i == 0 && j == 0) {
          //   targetNum += this.data[i].children[0].children[cheakList[i][j]].value;
            
          // }
          // targetNum += '|' + this.data[i].children[0].children[cheakList[i][j]].value;
        }
      }
      const params = {
        orderId: this.id,
        boostingTask: data,
        operateType: "ADD",
        progress: JSON.stringify(pro),
        target: data,

        businessType: "GatherMaterials"
      };
      this.$api.taskBoosting(params).then((res) => {
        const body = res.data;
        if (body.code != "200") {
          this.$message.success("成功！");
        } else {
          this.$message.error(body.message);
        }
      });
    },
  },
};
</script>

